export interface ReserveType {
  value: number
  text: string
  desc: string
  price: number
}

export interface IdType {
  label: string
  value: number | null
}

/**
 * 校验购买人姓名
 * @param name
 * @returns
 */
export function checkName(name: string) {
  if (name === '') {
    return false
  }
  if (!/^[a-zA-Z\u4E00-\u9FA5\uf900-\ufa2d·s]{2,20}$/.test(name)) {
    uni.showToast({ title: '姓名填写错误', duration: 1500, icon: 'none' })
    return false
  }
  return true
}
/**
 * 校验购买人手机号
 * @param phone
 * @returns
 */
export function checkPhone(phone: string) {
  if (phone === '') {
    return false
  }
  if (!/^1[3456789]\d{9}$/.test(phone)) {
    uni.showToast({
      title: '手机号格式错误',
      duration: 1500,
      icon: 'none'
    })
    return false
  }
  return true
}
/**
 * 校验身份证号
 * @param idCard
 * @returns
 */
export function checkIdCard(idCard: string) {
  if (idCard === '') {
    return false
  }
  // 15位和18位身份证号码的正则表达式
  const regIdCard = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/
  // 如果通过该验证，说明身份证格式正确，但准确性还需计算
  if (regIdCard.test(idCard)) {
    if (idCard.length === 18) {
      const idCardWi = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2] // 将前17位加权因子保存在数组里
      const idCardY = [1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2] // 这是除以11后，可能产生的11位余数、验证码，也保存成数组
      let idCardWiSum = 0 // 用来保存前17位各自乖以加权因子后的总和
      for (let i = 0; i < 17; i++) {
        idCardWiSum += Number(idCard.substring(i, i + 1)) * idCardWi[i]
      }
      const idCardMod = idCardWiSum % 11 // 计算出校验码所在数组的位置
      const idCardLast = idCard.substring(17) // 得到最后一位身份证号码
      // 如果等于2，则说明校验码是10，身份证号码最后一位应该是X
      if (idCardMod === 2) {
        if (idCardLast === 'X' || idCardLast === 'x') {
          // 恭喜通过验证啦
          return true
        } else {
          // 身份证号码错误
          uni.showToast({
            title: '身份证号码错误',
            duration: 1000,
            icon: 'none'
          })
          return false
        }
      } else {
        // 用计算出的验证码与最后一位身份证号码匹配，如果一致，说明通过，否则是无效的身份证号码
        if (String(idCardLast) === String(idCardY[idCardMod])) {
          // 恭喜通过验证啦
          return true
        } else {
          // 身份证号码错误
          uni.showToast({
            title: '身份证号码错误',
            duration: 1000,
            icon: 'none'
          })
          return false
        }
      }
    }
  } else {
    // 身份证格式不正确
    uni.showToast({
      title: '身份证格式不正确',
      duration: 1000,
      icon: 'none'
    })
    return false
  }
}
