<template>
  <view class="uni-file-picker__container">
    <view class="file-picker__box" v-for="(item, index) in filesList" :key="index" :style="boxStyle">
      <view class="file-picker__box-content" :style="borderStyle">
        <video
          id="myVideo"
          class="file-video"
          v-if="item.fileType === 'video'"
          :src="item.path"
          object-fit="contain"
          @play="playVideo"
          @fullscreenchange="fullscreenchange"
        ></video>
        <image class="file-image" v-if="item.fileType === 'image'" :src="item.url" mode="aspectFill" @click.stop="prviewImage(item, index)"></image>
        <view v-if="delIcon && !readonly" class="icon-del-box" @click.stop="delFile(index)">
          <view class="icon-del"></view>
          <view class="icon-del rotate"></view>
        </view>
        <view v-if="(item.progress && item.progress !== 100) || item.progress === 0" class="file-picker__progress">
          <progress
            class="file-picker__progress-item"
            :percent="item.progress === -1 ? 0 : item.progress"
            stroke-width="4"
            :backgroundColor="item.errMsg ? '#ff5a5f' : '#EBEBEB'"
          />
        </view>
        <view v-if="item.errMsg" class="file-picker__mask" @click.stop="uploadFiles(item, index)">点击重试</view>
      </view>
    </view>
    <view v-if="filesList.length < limit && !readonly" class="file-picker__box" :style="boxStyle">
      <view class="file-picker__box-content is-add" :style="borderStyle" @click="choose">
        <slot>
          <image :src="assessUpload" width="64" height="64" />
          <view class="icon-add rotate"></view>
        </slot>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  name: 'uploadImage',
  inject: ['$gdp'],
  props: {
    filesList: {
      type: Array,
      default() {
        return []
      }
    },
    disabled: {
      type: Boolean,
      default: false
    },
    disablePreview: {
      type: Boolean,
      default: false
    },
    limit: {
      type: [Number, String],
      default: 6
    },
    imageStyles: {
      type: Object,
      default() {
        return {
          width: 'auto',
          height: 'auto',
          border: {}
        }
      }
    },
    delIcon: {
      type: Boolean,
      default: true
    },
    readonly: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    assessUpload() {
      // return this.$gdp('assess-upload.png')
    },
    styles() {
      let styles = {
        width: 'auto',
        height: 'auto',
        border: {}
      }
      return Object.assign(styles, this.imageStyles)
    },
    boxStyle() {
      const { width = 'auto', height = 'auto' } = this.styles
      let obj = {}
      if (height === 'auto') {
        if (width !== 'auto') {
          obj.height = this.value2px(width)
          obj['padding-top'] = 0
        } else {
          obj.height = 0
        }
      } else {
        obj.height = this.value2px(height)
        obj['padding-top'] = 0
      }

      if (width === 'auto') {
        if (height !== 'auto') {
          obj.width = this.value2px(height)
        } else {
          obj.width = '33.3%'
        }
      } else {
        obj.width = this.value2px(width)
      }

      let classles = ''
      for (let i in obj) {
        classles += `${i}:${obj[i]};`
      }
      return classles
    },
    borderStyle() {
      let { border } = this.styles
      let obj = {}
      if (typeof border === 'boolean') {
        obj.border = border ? '1px #eee dashed' : 'none'
      } else {
        let width = (border && border.width) || 1
        width = this.value2px(width)
        let radius = (border && border.radius) || 5
        radius = this.value2px(radius)
        obj = {
          'border-width': width,
          'border-style': (border && border.style) || 'dashed',
          'border-color': (border && border.color) || '#eee',
          'border-radius': radius
        }
      }
      let classles = ''
      for (let i in obj) {
        classles += `${i}:${obj[i]};`
      }
      return classles
    }
  },
  data() {
    return {}
  },
  methods: {
    playVideo(e) {
      this.videoContext = uni.createVideoContext('myVideo', this)
      this.videoContext.requestFullScreen({ direction: 0 })
    },
    fullscreenchange(e) {
      if (!e.detail.fullScreen) this.videoContext.pause()
    },
    uploadFiles(item, index) {
      this.$emit('uploadFiles', item)
    },
    choose() {
      this.$emit('choose')
    },
    delFile(index) {
      this.$emit('delFile', index)
    },
    prviewImage(img, index) {
      let urls = []
      if (Number(this.limit) === 1 && this.disablePreview && !this.disabled) {
        this.$emit('choose')
      }
      if (this.disablePreview) return
      this.filesList.forEach((i) => {
        urls.push(i.path)
      })

      uni.previewImage({
        urls: urls,
        current: index
      })
    },
    value2px(value) {
      if (typeof value === 'number') {
        value += 'px'
      } else {
        if (value.indexOf('%') === -1) {
          value = value.indexOf('px') !== -1 ? value : value + 'px'
        }
      }
      return value
    }
  }
}
</script>

