<script lang="ts">
import TraceKit from 'tracekit'
import { checkUpdateVersion } from './util/common'
export default {
  onLaunch: function () {
    console.log('App Launch')
    TraceKit.report.subscribe((errorReport) => {
      const { message, stack } = errorReport || {}
      const obj = {
        msg: message,
        url: stack[0].url,
        row: stack[0].line,
        col: stack[0].column
        // stack: {
        //   column: stack[0].column,
        //   line: stack[0].line,
        //   func: stack[0].func,
        //   url: stack[0].url,
        // },
      }

      console.log('TraceKit上报信息：', obj)
      // report(obj);
    })

    this.a = '121'
    console.log('this.a.b', this.a.b.ddd)
  },
  onShow: function () {
    console.log('App Show')
    checkUpdateVersion()
  },
  onHide: function () {
    console.log('App Hide')
  },
  onError: function (err) {
    console.log('App onError', err)
    TraceKit.report(err)
  }
}
</script>

